var server = window.location.hostname;
var port = "1337";
var Graph = /** @class */ (function () {
    function Graph(vector, id_graph) {
        this.vector = vector;
        this.id_graph = id_graph;
    }
    return Graph;
}());
var add_button = document.getElementById('add');
var execute_operation = document.getElementById('save');
var target = document.getElementById('forms');
var forms = document.getElementById('target');
var status_request = document.getElementById("status_request");
var vector = document.getElementById("vector");
var parent_vector = document.getElementById("parent_vector");
function getValueAndConvertToNumber() {
    var vector = [];
    for (var _i = 0, _a = Array.prototype.slice.call(forms.getElementsByTagName("input")); _i < _a.length; _i++) {
        var value = _a[_i];
        console.log(value);
        vector.push(Number(value.value));
    }
    return vector;
}
function getChildVector() {
    return parseInt(parent_vector.value);
}
function addElement() {
    forms.insertAdjacentHTML('beforeend', "<input placeholder=\"id child vertices\">");
}
function createVector() {
    var parent_vectors = getValueAndConvertToNumber();
    var child_vector = getChildVector();
    var obj = { parent_vectors: parent_vectors, child_vector: child_vector };
    console.log(obj);
    fetch("http://" + server + ":" + port + "/parent/", { method: "POST", headers: {
            'Accept': 'application/json, text/plain',
            'Content-Type': 'application/json;charset=UTF-8'
        }, body: JSON.stringify(obj) })
        .then(function (response) {
        return response.json().then(function (data) { return ({ status: response.status, body: data }); });
    }).then(function (data) {
        if (data.status == 200) {
            status_request.innerHTML = "";
            vector.innerHTML = data.body.vector;
        }
        else {
            status_request.innerHTML = "Did not work out";
        }
    });
}
add_button.addEventListener('click', addElement);
execute_operation.addEventListener('click', createVector);
